import React from 'react';
import QueueAnim from 'rc-queue-anim';
import Basic from './Basic';
import BorderTitleAndFooter from './BorderTitleAndFooter';
import ColSpanAndRowSpan from './ColSpanAndRowSpan';
import ExpandableRow from './ExpandableRow';
import FixedColumns from './FixedColumns';
import FixedColumnsAndHeader from './FixedColumnsAndHeader';
import FixedHeader from './FixedHeader';
import GroupingTableHead from './GroupingTableHead';
import NestedTables from './NestedTables';
import ResetFiltersAndSorters from './ResetFiltersAndSorters';
import Selection from './Selection';
import Size from './Size';
import TreeData from './TreeData';

const Page = () => {
  return(
    <div className="container-fluid with-maxwidth chapter">

      <QueueAnim type="bottom" className="ui-animate">
        <div key="1"> <Basic /> </div>
        <div key="2"> <BorderTitleAndFooter /> </div>
        <div key="3"> <ColSpanAndRowSpan /> </div>
        <div key="4"> <ExpandableRow /> </div>
        <div key="5"> <FixedColumns /> </div>
        <div key="6"> <FixedColumnsAndHeader /> </div>
        <div key="7"> <FixedHeader /> </div>
        <div key="8"> <GroupingTableHead /> </div>
        <div key="9"> <NestedTables /> </div>
        <div key="10"> <ResetFiltersAndSorters /> </div>
        <div key="11"> <Selection /> </div>
        <div key="12"> <Size /> </div>
        <div key="13"> <TreeData /> </div>
      </QueueAnim>

    </div>
  )
}

module.exports = Page;


